function saveFormattingModel() {
    const formattingModel = document.getElementById('formatting-model').value;
    localStorage.setItem('formattingModel', formattingModel);
}

function saveApplyFormatting() {
    const applyFormatting = document.getElementById('apply-formatting').checked;
    localStorage.setItem('applyFormatting', applyFormatting);
}

function loadSettings() {
    const apiKey = localStorage.getItem('apiKey') || '';
    const formattingModel = localStorage.getItem('formattingModel') || 'openai/gpt-4-mini';
    const applyFormatting = localStorage.getItem('applyFormatting') === 'true';

    document.getElementById('api-key').value = apiKey;
    $('#formatting-model').val(formattingModel).trigger('change');
    document.getElementById('apply-formatting').checked = applyFormatting;
}



function populateModelSelectt(models) {
    const promptModelSelect = $('#prompt-model');
    const formattingModelSelect = $('#formatting-model');

    [promptModelSelect, formattingModelSelect].forEach(select => {
        select.empty();
        models.forEach(model => {
            const option = new Option(model.name, model.id, false, false);
            select.append(option);
        });
        select.trigger('change');
    });

    //      formatting-model
    const defaultModel = models.find(model => model.id === 'openai/gpt-4-mini') || models[0];
    if (defaultModel) {
        formattingModelSelect.val(defaultModel.id).trigger('change');
    }

    //   ,   
    const savedModel = localStorage.getItem('formattingModel');
    if (savedModel && models.some(model => model.id === savedModel)) {
        formattingModelSelect.val(savedModel).trigger('change');
    }
}



function saveApiKey() {
    const apiKey = document.getElementById('api-key').value;
    localStorage.setItem('apiKey', apiKey);
}




const OPENROUTER_BASE_URL = "https://openrouter.ai/api/v1";

async function loadModels() {
    try {
        const response = await axios.get(`${OPENROUTER_BASE_URL}/models`);
        const models = response.data.data;
        localStorage.setItem('models', JSON.stringify(models));
        populateModelSelectt(models);
    } catch (error) {
        console.error('Error loading models:', error);
    }
}

async function generateTextWithAI(systemPrompt, userPrompt, model, signal) {
    const apiKey = localStorage.getItem('apiKey');

    try {
        const response = await fetch(`${OPENROUTER_BASE_URL}/chat/completions`, {
            method: 'POST',
            signal,
            headers: {
                'Authorization': `Bearer ${apiKey}`,
                'Content-Type': 'application/json',
                'HTTP-Referer': 'https://www.office.com',
                'X-Title': 'Word AI Assistant',
            },
            body: JSON.stringify({
                model: model,
                messages: [
                    { role: "system", content: systemPrompt },
                    { role: "user", content: userPrompt }
                ],
            }),
        });

        if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`);
        }

        const data = await response.json();
        return data.choices[0].message.content.trim();
    } catch (error) {
        console.error('Error in generateTextWithAI:', error);
        throw error;
    }
}

async function generateTextWithAIFormat(systemPrompt, userPrompt, model, signal) {
    const apiKey = localStorage.getItem('apiKey');

    try {
        const response = await fetch(`${OPENROUTER_BASE_URL}/chat/completions`, {
            method: 'POST',
            signal,
            headers: {
                'Authorization': `Bearer ${apiKey}`,
                'Content-Type': 'application/json',
                'HTTP-Referer': 'https://www.office.com',
                'X-Title': 'Word AI Assistant',
            },
            body: JSON.stringify({
                model: model,
                messages: [
                    { role: "system", content: systemPrompt },
                    { role: "user", content: userPrompt }
                ],
            }),
        });

        if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`);
        }

        const data = await response.json();
        return data.choices[0].message.content.trim();
    } catch (error) {
        console.error('Error in generateTextWithAI:', error);
        throw error;
    }
}



let originalText = "";
let currentController = null;
let lastSelectedPrompt = null;

function initHome() {
  const homeTab = document.getElementById('home');
  homeTab.innerHTML = `
      <select id="prompt-select" class="form-select mt-3"></select>
      <div id="selected-model" class="mt-2"></div>
      <button id="replace-text" class="btn btn-primary mt-3" disabled>  </button>
      <button id="cancel-request" class="btn btn-secondary mt-3" style="display: none;"> </button>
      <button id="show-selected-text" class="btn btn-info mt-3">    </button>
      <!-- <button id="apply-tricks" class="btn btn-warning mt-3"> </button>
      <button id="repeat-styles" class="btn btn-primary mt-3"> </button> -->
      <div id="selected-text-info" class="mt-3"></div>
  `;

  $('#prompt-select').select2({
      width: '100%',
      placeholder: ' ',
      templateResult: formatPrompt,
      templateSelection: formatPromptSelection,
  });

  document.getElementById("replace-text").onclick = replaceSelectedText;
  document.getElementById("cancel-request").onclick = cancelRequest;
  document.getElementById("show-selected-text").onclick = showSelectedText;
  // document.getElementById("apply-tricks").onclick = applyTricks;
  // document.getElementById("repeat-styles").onclick = repeatStyles;

  updatePromptSelect();
  setInterval(checkSelection, 1000);
}

function formatPrompt(prompt) {
    if (!prompt.id) {
        return prompt.text;
    }
    const prompts = getPrompts();
    const promptData = prompts[prompt.id];
    return $(`<span>${promptData.name} (${promptData.model})</span>`);
}

function formatPromptSelection(prompt) {
    if (!prompt.id) {
        return prompt.text;
    }
    const prompts = getPrompts();
    const promptData = prompts[prompt.id];
    return promptData.name;
}

function updatePromptSelect() {
    const select = $('#prompt-select');
    const modelDiv = document.getElementById('selected-model');
    const prompts = getPrompts();
    select.empty();
    select.append(new Option(' ', '', true, true));
    const lastSelectedPromptIndex = getLastSelectedPromptIndex();
    prompts.forEach((prompt, index) => {
        const option = new Option(prompt.name, index, false, index === lastSelectedPromptIndex);
        select.append(option);
    });
    select.trigger('change');
    const promptIndex = lastSelectedPromptIndex >= 0 && lastSelectedPromptIndex < prompts.length ? lastSelectedPromptIndex : null;
    lastSelectedPrompt = promptIndex !== null ? prompts[promptIndex] : null;
    modelDiv.textContent = lastSelectedPrompt ? `: ${lastSelectedPrompt.model}` : '';
    document.getElementById("replace-text").disabled = !lastSelectedPrompt;
    select.on('change', function() {
        const newPromptIndex = $(this).val();
        lastSelectedPrompt = newPromptIndex ? prompts[newPromptIndex] : null;
        saveLastSelectedPromptIndex(newPromptIndex || -1);
        modelDiv.textContent = lastSelectedPrompt ? `: ${lastSelectedPrompt.model}` : '';
        document.getElementById("replace-text").disabled = !lastSelectedPrompt;
    });
}

function checkSelection() {
    Word.run(async (context) => {
        const selection = context.document.getSelection();
        selection.load("text");
        await context.sync();

        const replaceButton = document.getElementById("replace-text");
        if (selection.text.trim().length > 0) {
            replaceButton.disabled = false;
        } else {
            replaceButton.disabled = true;
        }
    });
}

async function replaceSelectedText() {
  Word.run(async (context) => {
      const selection = context.document.getSelection();
      selection.load("text");
      await context.sync();

      originalText = selection.text;

      if (!lastSelectedPrompt) return;

      document.getElementById("replace-text").style.display = "none";
      document.getElementById("cancel-request").style.display = "inline-block";

      try {
          currentController = new AbortController();

          const applyFormatting = localStorage.getItem('applyFormatting') === 'true';
          const formattingModel = localStorage.getItem('formattingModel') || 'openai/gpt-4-mini';
          let formattingInfo = [];

          if (applyFormatting) {
              formattingInfo = await getWordsInfo(context, selection);
          }

          const systemPrompt = `You are an AI assistant specialized in text processing and formatting. Your task is to generate text based on user prompts and, when required, update formatting information for the generated text. Always provide responses in the requested format.`;

          const userPrompt = `${lastSelectedPrompt.text}\n\nUser provided the following text: "${originalText}"${applyFormatting ? `\n\nFormatting information: ${JSON.stringify(formattingInfo)}` : ''}`;

          const generatedText = await generateTextWithAI(systemPrompt, userPrompt, lastSelectedPrompt.model, currentController.signal);

          if (applyFormatting) {
              const secondSystemPrompt = `You are an AI assistant specialized in text formatting. Your task is to update formatting information for modified text while preserving the original styles as much as possible. Always return your response as a valid JSON array.`;

              const secondUserPrompt = `
Original text: "${originalText}"
Modified text: "${generatedText}"
Original formatting information: ${JSON.stringify(formattingInfo)}

Task: Update the formatting information to match the new text while preserving the original styles and formats. Return ONLY the updated formatting information array in valid JSON format.

Instructions:
1. Analyze the original and modified texts.
2. Create a new array of formatting information for the modified text.
3. Preserve the original formatting styles as much as possible.
4. Ensure the number of elements in the array matches the number of words in the modified text.
5. Return ONLY the JSON array, without any additional text or explanation.

The JSON array should contain objects with the following possible properties:
- text: string (required) - the word from the modified text
- bold: boolean
- italic: boolean
- underline: boolean
- color: string (e.g., "#FF0000" for red)
- highlightColor: string (e.g., "yellow")
- size: number
- fontName: string
- hyperlink: string (URL)
- footnoteText: string

Example of a valid response:
[
{"text": "This", "bold": true, "color": "#0000FF"},
{"text": "is", "italic": true},
{"text": "an", "underline": true},
{"text": "example", "size": 14, "fontName": "Arial"},
{"text": "with", "highlightColor": "yellow"},
{"text": "formatting", "hyperlink": "https://example.com"},
{"text": "applied", "footnoteText": "This is a footnote example"}
]

Ensure your response is a valid JSON array and includes formatting information for all words in the modified text.`;

              let updatedFormattingInfo;
              try {
                  const formattingResponse = await generateTextWithAIFormat(secondSystemPrompt, secondUserPrompt, formattingModel, currentController.signal);
                  updatedFormattingInfo = JSON.parse(formattingResponse);

                  if (!Array.isArray(updatedFormattingInfo)) {
                      throw new Error("Response is not an array");
                  }
              } catch (error) {
                  console.error("Error parsing formatting information:", error);
                  selection.insertText(generatedText, Word.InsertLocation.replace);
                  return;
              }

              await applyFormattedText(context, selection, generatedText, updatedFormattingInfo);
          } else {
              selection.insertText(generatedText, Word.InsertLocation.replace);
          }
      } catch (error) {
          if (error.name !== 'AbortError') {
              console.error('Error:', error);
          }
      } finally {
          document.getElementById("cancel-request").style.display = "none";
          document.getElementById("replace-text").style.display = "inline-block";
          currentController = null;
      }
  });
}


async function applyFormattedText(context, selection, newText, formattingInfo) {
  selection.clear();
  await context.sync();

  const words = newText.split(/\s+/);
  let wordIndex = 0;

  for (let i = 0; i < words.length; i++) {
      let word = words[i];
      let range = selection.insertText(word, Word.InsertLocation.end);

      if (wordIndex < formattingInfo.length) {
          const wordInfo = formattingInfo[wordIndex];

          if (wordInfo.bold !== undefined) range.font.bold = wordInfo.bold;
          if (wordInfo.italic !== undefined) range.font.italic = wordInfo.italic;
          if (wordInfo.color) range.font.color = wordInfo.color;
          if (wordInfo.highlightColor) range.font.highlightColor = wordInfo.highlightColor;
          if (wordInfo.size) range.font.size = wordInfo.size;

          if (wordInfo.hyperlink) {
              range.hyperlink = wordInfo.hyperlink;
          }

          if (wordInfo.footnoteText) {
              let newFootnote = range.insertFootnote();
              newFootnote.body.insertText(wordInfo.footnoteText, Word.InsertLocation.start);
          }

          wordIndex++;
      }

      if (i < words.length - 1) {
          selection.insertText(" ", Word.InsertLocation.end);
      }

      await context.sync();
  }
}


function cancelRequest() {
    if (currentController) {
        currentController.abort();
        document.getElementById("cancel-request").style.display = "none";
        document.getElementById("replace-text").style.display = "inline-block";
    }
}

async function applyTricks() {
  await Word.run(async (context) => {
    const selection = context.document.getSelection();
    selection.load("text");
    await context.sync();

    const words = selection.text.trim().split(/\s+/);
    if (words.length < 4) {
      console.log("  4 ");
      return;
    }

    const range = selection.getRange();
    range.clear();
    await context.sync();

    for (let i = 0; i < words.length; i++) {
      const word = words[i];
      let wordRange = range.insertText(word + " ", Word.InsertLocation.end);

      wordRange.font.bold = false;
      wordRange.font.italic = false;
      wordRange.font.color = 'black';
      wordRange.font.highlightColor = "white";
      wordRange.font.size = 11;

      if (i === 0) {
        wordRange.font.bold = true;
      }

      if (i === 1) {
        wordRange.font.italic = true;
      }

      if (i === 2) {
        wordRange.hyperlink = "https://www.example.com";
      }

      if (i === 3) {
        wordRange.font.color = 'red';
      }

      if (i === 4) {
        wordRange.font.highlightColor = 'yellow';
      }

      if (i === 5) {
        wordRange.font.size = 16;
      }

      if (i === words.length - 1) {
        let footnote = wordRange.insertFootnote();
        footnote.body.insertText("  ", Word.InsertLocation.start);
      }

      await context.sync();
    }
  });
}

function formatSpecialWords(wordsInfo, baseColor, baseFont, baseSize) {
  let formattedText = [];
  let footnotes = [];

  wordsInfo.forEach((word, index) => {
    let wordData = { text: word.text };
    let hasSpecialFormatting = false;

    let formatting = {};

    if (word.bold === true || word.bold === null) {
      formatting.bold = true;
      hasSpecialFormatting = true;
    }
    if (word.italic) {
      formatting.italic = true;
      hasSpecialFormatting = true;
    }
    if (word.underline) {
      formatting.underline = true;
      hasSpecialFormatting = true;
    }
    if (word.color !== baseColor && word.color !== "") {
      formatting.color = word.color;
      hasSpecialFormatting = true;
    }
    if (word.highlightColor && word.highlightColor !== null) {
      formatting.highlight = word.highlightColor;
      hasSpecialFormatting = true;
    }
    if (word.fontName !== baseFont && word.fontName !== "") {
      formatting.font = word.fontName;
      hasSpecialFormatting = true;
    }
    if (word.fontSize != baseSize && word.fontSize !== "" && word.fontSize !== null) {
      formatting.size = word.fontSize;
      hasSpecialFormatting = true;
    }
    if (word.hyperlink) {
      formatting.hyperlink = word.hyperlink;
      hasSpecialFormatting = true;
    }

    if (Object.keys(formatting).length > 0) {
      wordData.formatting = formatting;
    }

    if (word.hasFootnote) {
      wordData.footnoteId = index + 1;
      footnotes.push({
        id: index + 1,
        text: word.footnoteText,
        relatedWordIndex: formattedText.length
      });
      hasSpecialFormatting = true;
    }

    if (hasSpecialFormatting) {
      formattedText.push(wordData);
    }
  });

  return {
    formattedText: formattedText,
    footnotes: footnotes,
    baseFormatting: {
      color: baseColor,
      font: baseFont,
      size: baseSize
    }
  };
}

async function showSelectedText() {
  await Word.run(async (context) => {
    const selection = context.document.getSelection();
    const ranges = selection.getTextRanges([" "], false);
    ranges.load("items");

    await context.sync();

    let wordsInfo = [];
    let colorCounts = {};
    let fontCounts = {};
    let sizeCounts = {};

    for (let i = 0; i < ranges.items.length; i++) {
      const range = ranges.items[i];
      range.load("text, hyperlink");
      range.font.load("bold, italic, underline, color, highlightColor, name, size");

      const footnotes = range.footnotes;
      footnotes.load("items");

      await context.sync();

      if (range.text.trim()) {
        const color = range.font.color || "default";
        const font = range.font.name || "default";
        const size = range.font.size || "default";

        colorCounts[color] = (colorCounts[color] || 0) + 1;
        fontCounts[font] = (fontCounts[font] || 0) + 1;
        sizeCounts[size] = (sizeCounts[size] || 0) + 1;

        let footnoteText = "";
        if (footnotes.items.length > 0) {
          const footnote = footnotes.items[0];
          footnote.body.load("text");
          await context.sync();
          footnoteText = footnote.body.text;
        }

        wordsInfo.push({
          text: range.text.trim(),
          bold: range.font.bold,
          italic: range.font.italic,
          underline: range.font.underline !== "None",
          color: color,
          highlightColor: range.font.highlightColor,
          hasFootnote: footnotes.items.length > 0,
          footnoteText: footnoteText,
          fontName: font,
          fontSize: size,
          hyperlink: range.hyperlink ? range.hyperlink.address : null,
          isFirstWord: i === 0,
          isSecondWord: i === 1,
          isThirdWord: i === 2,
          isLastWord: i === ranges.items.length - 1
        });
      }
    }

    const baseColor = Object.entries(colorCounts).reduce((a, b) => a[1] > b[1] ? a : b)[0];
    const baseFont = Object.entries(fontCounts).reduce((a, b) => a[1] > b[1] ? a : b)[0];
    const baseSize = Object.entries(sizeCounts).reduce((a, b) => a[1] > b[1] ? a : b)[0];

    displayWordsInfo(wordsInfo, baseColor, baseFont, baseSize);
    const formattedMassive = formatSpecialWords(wordsInfo, baseColor, baseFont, baseSize);
    console.log(formattedMassive);
  });
}

function displayWordsInfo(wordsInfo, baseColor, baseFont, baseSize) {
  const infoDiv = document.getElementById("selected-text-info");
  let html = "<h3>  :</h3>";

  wordsInfo.forEach((word, index) => {
    html += `<h4> ${index + 1}: ${word.text}</h4>`;
    html += `<p>
      ${word.bold ? "" : ""} ${word.isFirstWord ? " ( )" : ""}<br>
      ${word.italic ? "" : ""} ${word.isSecondWord ? " ( )" : ""}<br>
      ${word.underline ? "" : ""} <br>
      ${word.color !== baseColor && word.color !== "default" ? `  : ${word.color}` : "  "}<br>
      ${word.highlightColor ? ` : ${word.highlightColor}` : "  "}<br>
      ${word.hasFootnote ? `  : ${word.footnoteText}${word.isLastWord ? " ( )" : ""}` : "  "}<br>
      ${word.fontName !== baseFont && word.fontName !== "default" ? `  : ${word.fontName}` : `  : ${baseFont}`}<br>
      ${word.fontSize != baseSize && word.fontSize !== "default" ? `  : ${word.fontSize}` : `  : ${baseSize}`}<br>
      ${word.hyperlink ? ` : ${word.hyperlink}${word.isThirdWord ? " ( )" : ""}` : "  "}
    </p>`;
  });

  infoDiv.innerHTML = html;
}

async function repeatStyles() {
  await Word.run(async (context) => {
    const selection = context.document.getSelection();
    selection.load("text");
    await context.sync();

    const originalText = selection.text;
    let wordsInfo = await getWordsInfo(context, selection);

    const baseFormatting = getBaseFormatting(wordsInfo);
    wordsInfo = optimizeWordsInfo(wordsInfo, baseFormatting);
    wordsInfo = wordsInfo.filter(wordInfo => Object.keys(wordInfo).length > 1);

    console.log(wordsInfo);
    console.log("Base formatting:", baseFormatting);

    selection.clear();
    await context.sync();

    const words = originalText.split(/\s+/);
    let wordIndex = 0;

    for (let i = 0; i < words.length; i++) {
      let word = words[i];

      // if (word == "") {
      //   word = " "
      // }

      let range = selection.insertText(word, Word.InsertLocation.end);

      //   
      range.font.bold = baseFormatting.bold;
      range.font.italic = baseFormatting.italic;
      range.font.color = baseFormatting.color;
      range.font.highlightColor = baseFormatting.highlightColor;
      range.font.size = baseFormatting.size;

      if (wordIndex < wordsInfo.length && wordsInfo[wordIndex].text === word) {
        const wordInfo = wordsInfo[wordIndex];

        if (wordInfo.bold !== undefined) range.font.bold = wordInfo.bold;
        if (wordInfo.italic !== undefined) range.font.italic = wordInfo.italic;
        if (wordInfo.color) range.font.color = wordInfo.color;
        if (wordInfo.highlightColor) range.font.highlightColor = wordInfo.highlightColor;
        if (wordInfo.size) range.font.size = wordInfo.size;

        if (wordInfo.hyperlink) {
          range.hyperlink = wordInfo.hyperlink;
        }

        if (wordInfo.footnoteText) {
          let newFootnote = range.insertFootnote(wordInfo.footnoteText.replace("", ' '), Word.InsertLocation.start);
          // newFootnote.body.insertText(wordInfo.footnoteText.replace("", ''), Word.InsertLocation.start);
          word = word.replace("", ' ');
        }



        wordIndex++;
      }

      if (i < words.length - 1) {
        selection.insertText(" ", Word.InsertLocation.end);
      }

      await context.sync();
    }
  });
}

function getBaseFormatting(wordsInfo) {
  return {
    bold: getMostCommonValue(wordsInfo, 'bold'),
    italic: getMostCommonValue(wordsInfo, 'italic'),
    color: getMostCommonValue(wordsInfo, 'color'),
    highlightColor: getMostCommonValue(wordsInfo, 'highlightColor'),
    size: getMostCommonValue(wordsInfo, 'size')
  };
}

function optimizeWordsInfo(wordsInfo, baseFormatting) {
  return wordsInfo.map(word => {
    const optimizedWord = { text: word.text };

    if (word.bold !== baseFormatting.bold) optimizedWord.bold = word.bold;
    if (word.italic !== baseFormatting.italic) optimizedWord.italic = word.italic;
    if (word.color !== baseFormatting.color) optimizedWord.color = word.color;
    if (word.highlightColor !== baseFormatting.highlightColor) optimizedWord.highlightColor = word.highlightColor;
    if (word.size !== baseFormatting.size) optimizedWord.size = word.size;

    if (word.hasFootnote) optimizedWord.footnoteText = word.footnoteText;
    if (word.hyperlink) optimizedWord.hyperlink = word.hyperlink;

    return optimizedWord;
  });
}




async function getWordsInfo(context, selection) {
  const ranges = selection.getTextRanges([" "], false);
  ranges.load("items");
  await context.sync();

  let wordsInfo = [];

  for (let range of ranges.items) {
    range.load("text, hyperlink");
    range.font.load("bold, italic, color, highlightColor, size");
    const footnotes = range.footnotes;
    footnotes.load("items");

    await context.sync();

    if (range.text.trim()) {
      let footnoteText = "";
      if (footnotes.items.length > 0) {
        const footnote = footnotes.items[0];
        footnote.body.load("text");
        await context.sync();
        footnoteText = footnote.body.text;
      }

      wordsInfo.push({
        text: range.text.trim(),
        bold: range.font.bold,
        italic: range.font.italic,
        color: range.font.color || "black",
        highlightColor: range.font.highlightColor || "white",
        size: range.font.size || "11",
        hasFootnote: footnotes.items.length > 0,
        footnoteText: footnoteText,
        hyperlink: range.hyperlink ? range.hyperlink : null
      });
    }
  }

  return wordsInfo;
}


function getMostCommonValue(array, property) {
  const counts = {};
  let maxCount = 0;
  let mostCommonValue;

  array.forEach(item => {
    const value = item[property];
    counts[value] = (counts[value] || 0) + 1;
    if (counts[value] > maxCount) {
      maxCount = counts[value];
      mostCommonValue = value;
    }
  });

  return mostCommonValue;
}





function initPrompts() {
    const promptsTab = document.getElementById('prompts');
    promptsTab.innerHTML = `
        <div class="mt-3">
            <input type="text" id="prompt-name" class="form-control" placeholder=" ">
            <select id="prompt-model" class="form-select mt-2"></select>
            <textarea id="prompt-text" class="form-control mt-2" rows="5" placeholder=" "></textarea>
            <button id="save-prompt" class="btn btn-primary mt-2"> </button>
        </div>
        <div id="prompt-list" class="mt-3"></div>
    `;

    $('#prompt-model').select2({
        width: '100%',
        placeholder: ' ',
    }).on('select2:opening', function() {
        setTimeout(() => {
            $('.select2-search__field').get(0).focus();
        }, 0);
    });

    document.getElementById("save-prompt").onclick = savePrompt;
    updatePromptList();
    populateModelSelect();
}

function populateModelSelect() {
    const select = $('#prompt-model');
    const models = JSON.parse(localStorage.getItem('models') || '[]');
    models.forEach(model => {
        const option = new Option(model.name, model.id, false, false);
        select.append(option);
    });
    select.trigger('change');
}

function savePrompt() {
    const name = document.getElementById('prompt-name').value;
    const model = $('#prompt-model').val();
    const text = document.getElementById('prompt-text').value;
    if (name && model && text) {
        const prompts = getPrompts();
        prompts.push({ name, model, text });
        savePrompts(prompts);
        updatePromptList();
        updatePromptSelect();
        document.getElementById('prompt-name').value = '';
        $('#prompt-model').val(null).trigger('change');
        document.getElementById('prompt-text').value = '';
    }
}

function updatePromptList() {
    const list = document.getElementById('prompt-list');
    const prompts = getPrompts();
    list.innerHTML = '';
    prompts.forEach((prompt, index) => {
        const div = document.createElement('div');
        div.className = 'mt-2';
        div.innerHTML = `
            <strong>${prompt.name}</strong> (${prompt.model})
            <button class="btn btn-sm btn-primary edit-prompt" data-index="${index}"></button>
            <button class="btn btn-sm btn-info duplicate-prompt" data-index="${index}"></button>
            <button class="btn btn-sm btn-danger delete-prompt" data-index="${index}"></button>
        `;
        list.appendChild(div);
    });

    list.querySelectorAll('.delete-prompt').forEach(button => {
        button.onclick = () => deletePrompt(button.dataset.index);
    });

    list.querySelectorAll('.edit-prompt').forEach(button => {
        button.onclick = () => editPrompt(button.dataset.index);
    });

    list.querySelectorAll('.duplicate-prompt').forEach(button => {
        button.onclick = () => duplicatePrompt(button.dataset.index);
    });
}

function deletePrompt(index) {
    const prompts = getPrompts();
    prompts.splice(index, 1);
    savePrompts(prompts);
    updatePromptList();
    updatePromptSelect();
}

function editPrompt(index) {
    const prompts = getPrompts();
    const prompt = prompts[index];
    document.getElementById('prompt-name').value = prompt.name;
    $('#prompt-model').val(prompt.model).trigger('change');
    document.getElementById('prompt-text').value = prompt.text;
    document.getElementById('save-prompt').textContent = ' ';
    document.getElementById('save-prompt').onclick = () => {
        updatePrompt(index);
        document.getElementById('save-prompt').textContent = ' ';
        document.getElementById('save-prompt').onclick = savePrompt;
    };
}

function updatePrompt(index) {
    const name = document.getElementById('prompt-name').value;
    const model = $('#prompt-model').val();
    const text = document.getElementById('prompt-text').value;
    if (name && model && text) {
        const prompts = getPrompts();
        prompts[index] = { name, model, text };
        savePrompts(prompts);
        updatePromptList();
        updatePromptSelect();
        document.getElementById('prompt-name').value = '';
        $('#prompt-model').val(null).trigger('change');
        document.getElementById('prompt-text').value = '';
    }
}

function duplicatePrompt(index) {
    const prompts = getPrompts();
    const prompt = prompts[index];
    document.getElementById('prompt-name').value = prompt.name + ' ()';
    $('#prompt-model').val(null).trigger('change');
    document.getElementById('prompt-text').value = prompt.text;
    document.getElementById('save-prompt').textContent = ' ';
    document.getElementById('save-prompt').onclick = savePrompt;
}

