function displayPromptMessage(message) {
    const messageDiv = document.getElementById('prompt-message');
    messageDiv.innerHTML = `<div class="alert alert-info">${message}</div>`;
    setTimeout(() => {
        messageDiv.innerHTML = '';
    }, 3000);
}

async function checkGrammar() {
    try {
        const splitIntoChunks = document.getElementById('split-into-chunks-checkbox').checked;
        const chunkSize = parseInt(document.getElementById('chunk-size').value, 10) || 5;

        //      
        document.getElementById('grammar-results').innerHTML = '';
        resetProgressBar();

        showLoading(true, splitIntoChunks);

        //   
        document.getElementById('filter-container').style.display = 'none';

        //   AbortController   
        window.abortController = new AbortController();

        //   
        const selectionText = await Word.run(async (context) => {
            const selection = context.document.getSelection();
            selection.load("text");
            await context.sync();
            return selection.text.trim();
        });

        const maxChars = parseInt(document.getElementById('max-chars').value, 10) || 100;

        if (selectionText.length > 0) {
            if (!splitIntoChunks && maxChars > 0 && selectionText.length > maxChars) {
                document.getElementById('grammar-results').innerHTML = `<p class="text-warning">    (${selectionText.length} ). ,   ${maxChars}      .</p>`;
                showLoading(false);
                return;
            }

            const model = $('#grammar-model').val();
            let result = [];

            if (splitIntoChunks) {
                const chunks = splitTextIntoChunks(selectionText, chunkSize);
                const totalChunks = chunks.length;
                const progressBar = document.getElementById('progress-bar');
                const progressText = document.getElementById('progress-text');

                //       
                progressText.innerText = ` 0  ${totalChunks} `;

                for (let i = 0; i < chunks.length; i++) {
                    const chunkResult = await analyzeTextWithRetry(chunks[i], model, window.abortController.signal);
                    result = result.concat(chunkResult);

                    //   
                    const progressPercent = Math.round(((i + 1) / totalChunks) * 100);
                    progressBar.style.width = `${progressPercent}%`;
                    progressBar.innerText = `${progressPercent}%`;
                    progressText.innerText = ` ${i + 1}  ${totalChunks} `;
                }
            } else {
                const chunkResult = await analyzeTextWithRetry(selectionText, model, window.abortController.signal);
                result = result.concat(chunkResult);
            }

            //       
            window.grammarResults = result;

            displayResults(result);
            await highlightText(result);

            //   ,   
            if (result.length > 0) {
                document.getElementById('filter-container').style.display = 'block';
            }
        } else {
            console.log("  ");
            document.getElementById('grammar-results').innerHTML = '<p class="text-warning">,    .</p>';
            showLoading(false); //  ,    
        }
    } catch (error) {
        if (error.name === 'AbortError') {
            console.log('  .');
            document.getElementById('grammar-results').innerHTML = '<p class="text-info"> .</p>';
        } else {
            console.error('Error in checkGrammar:', error);
            document.getElementById('grammar-results').innerHTML = '<p class="text-danger">   . ,   .</p>';
        }
    } finally {
        showLoading(false);
        window.abortController = null;
    }
}

function cancelCheck() {
    if (window.abortController) {
        window.abortController.abort();
        showLoading(false);
        window.abortController = null;
    }
}

function resetProgressBar() {
    const progressBar = document.getElementById('progress-bar');
    const progressText = document.getElementById('progress-text');
    if (progressBar) {
        progressBar.style.width = '0%';
        progressBar.innerText = '0%';
    }
    if (progressText) {
        progressText.innerText = ' 0  0 ';
    }
}

function splitTextIntoChunks(text, chunkSize) {
    const sentences = text.match(/[^.!?]+[.!?]+(\s|$)|[^.!?]+$/g) || [text];
    const chunks = [];
    for (let i = 0; i < sentences.length; i += chunkSize) {
        const chunk = sentences.slice(i, i + chunkSize).join(' ').trim();
        chunks.push(chunk);
    }
    return chunks;
}

async function analyzeTextWithRetry(text, model, signal) {
    const retryOnError = document.getElementById('retry-on-error-checkbox').checked;
    const retryCount = parseInt(document.getElementById('retry-count').value, 10) || 1;

    let attempts = 0;
    let lastError = null;

    while (attempts < retryCount) {
        try {
            const result = await analyzeText(text, model, signal);
            return result;
        } catch (error) {
            lastError = error;
            attempts++;
            if (!retryOnError || attempts >= retryCount) {
                throw error;
            } else {
                console.warn(` ${attempts}  . ...`);
            }
        }
    }

    throw lastError;
}

async function analyzeText(text, model, signal) {
    const apiKey = localStorage.getItem('apiKey');

    //    
    const errorTypes = [];
    const errorTypesMap = {};

    const prompts = {};
    const defaultPrompts = getDefaultPrompts();

    if (document.getElementById('check-grammatical').checked) {
        errorTypes.push('');
        errorTypesMap[''] = true;
        prompts[''] = document.getElementById('prompt-grammatical').value || defaultPrompts[''];
    }
    if (document.getElementById('check-syntactic').checked) {
        errorTypes.push('');
        errorTypesMap[''] = true;
        prompts[''] = document.getElementById('prompt-syntactic').value || defaultPrompts[''];
    }
    if (document.getElementById('check-stylistic').checked) {
        errorTypes.push('');
        errorTypesMap[''] = true;
        prompts[''] = document.getElementById('prompt-stylistic').value || defaultPrompts[''];
    }
    if (document.getElementById('check-logical').checked) {
        errorTypes.push('');
        errorTypesMap[''] = true;
        prompts[''] = document.getElementById('prompt-logical').value || defaultPrompts[''];
    }

    if (errorTypes.length === 0) {
        throw new Error('   ');
    }

    const errorTypesString = errorTypes.join(', ');

    //     
    const promptTextNote = document.getElementById('prompt-text-note').value || '';

    //   
    const defaultPromptsForRole = getDefaultPrompts();
    const promptRole = document.getElementById('prompt-role').value || defaultPromptsForRole['role'];

    //   
    const temperature = parseFloat(document.getElementById('temperature').value) || 0.1;
    const maxTokens = parseInt(document.getElementById('max-tokens').value, 10) || 8000;
    const responseFormat = document.getElementById('response-format-checkbox').checked;

    //   
    const systemPrompt = `${promptRole}   -         : ${errorTypesString}.

${promptTextNote ? `   : ${promptTextNote}\n` : ''}

${Object.keys(prompts).map(type => `  "${type}" :\n${prompts[type]}\n`).join('\n')}

 :

-        .

-     .

-    .

-             ,  "can_edit": true    "text_to_change"   .

-           ,  "can_edit": false,   "suggestion"        "example"    .

- ,     JSON  .  ,            (\\").

-     JSON-,    :
  1. "text":    (,   ).
  2. "comment":  .
  3. "errorType":   (${Object.keys(errorTypesMap).map(type => `"${type}"`).join(', ')}).
  4. "can_edit": true  false (,       ).
  5.  "can_edit"  true,   "text_to_change"   .
  6.  "can_edit"  false,   "suggestion"       "example"    .

 :

${getExampleErrors(Object.keys(errorTypesMap))}

:

-  ** JSON-     .

- ,     JSON  .  ,            (\").

-   ,   : [];
`;

    const userPrompt = `,   :

"${text}";
`;

    try {
        const requestBody = {
            model: model,
            messages: [
                { role: "system", content: systemPrompt },
                { role: "user", content: userPrompt }
            ],
            temperature: temperature,
            max_tokens: maxTokens,
        };

        if (responseFormat) {
            requestBody.response_format = { "type": "json_object" };
        }

        const response = await fetch("https://openrouter.ai/api/v1/chat/completions", {
            method: 'POST',
            headers: {
                'Authorization': `Bearer ${apiKey}`,
                'Content-Type': 'application/json',
                'HTTP-Referer': 'https://www.office.com',
                'X-Title': 'Word Grammar Checker',
            },
            body: JSON.stringify(requestBody),
            signal: signal, //     
        });

        if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`);
        }

        const data = await response.json();
        console.log('  :', data.choices[0].message.content.trim());

        //     JSON
        const jsonResponse = safeJsonParse(data.choices[0].message.content.trim());
        return jsonResponse;
    } catch (error) {
        if (error.name === 'AbortError') {
            console.log('  .');
            throw error;
        } else {
            console.error('Error in analyzeText:', error);
            throw error;
        }
    }
}

//     JSON
function safeJsonParse(str) {
    try {
        return JSON.parse(str);
    } catch (error) {
        //     
        const fixedStr = str.replace(/\\?"/g, '\\"').replace(/\\\\"/g, '\\\"');
        try {
            return JSON.parse(fixedStr);
        } catch (innerError) {
            console.error('Failed to parse JSON after fixing:', innerError);
            throw innerError;
        }
    }
}

